@font-face {
    font-family: 'Antenna-Light';
    src: url("/fonts/antenna/Antenna-Light.eot"); /* IE9 Compat Modes */
    src: url("/fonts/antenna/Antenna-Light.woff") format("woff"), /* Modern Browsers */
         url("/fonts/antenna/Antenna-Light.ttf")  format("truetype"), /* Safari, Android, iOS */
         url("/fonts/antenna/Antenna-Light.svg") format("svg"),
         url("/fonts/antenna/Antenna-Light.otf") format("opentype"); /* Legacy iOS */
}
@font-face {
    font-family: 'Antenna-Bold';
    src: url("/fonts/antenna/Antenna-Bold.eot"); /* IE9 Compat Modes */
    src: url("/fonts/antenna/Antenna-Bold.woff") format("woff"), /* Modern Browsers */
         url("/fonts/antenna/Antenna-Bold.ttf")  format("truetype"), /* Safari, Android, iOS */
         url("/fonts/antenna/Antenna-Bold.svg") format("svg"),
         url("/fonts/antenna/Antenna-Bold.otf") format("opentype"); /* Legacy iOS */
}
@font-face {
    font-family: 'Myriad';
    src: url("/fonts/myriad/MyriadWebPro.eot"); /* IE9 Compat Modes */
    src: url("/fonts/myriad/MyriadWebPro.woff") format("woff"), /* Modern Browsers */
         url("/fonts/myriad/MyriadWebPro.ttf")  format("truetype"), /* Safari, Android, iOS */
         url("/fonts/myriad/MyriadWebPro.svg") format("svg"),
         url("/fonts/myriad/MyriadWebPro.otf") format("opentype"); /* Legacy iOS */
}
a {
  text-decoration: none;
}

html{
  line-height: 1.5;

  @media only screen and (min-width: 0) {
    font-size: 14px;
  }

  @media only screen and (min-width: $medium-screen) {
    font-size: 14.5px;
  }

  @media only screen and (min-width: $large-screen) {
    font-size: 15px;
  }

  font-family: "Myriad", sans-serif;
  font-weight: normal;
  color: $off-black;
}
h1, h2, h3, h4, h5, h6 {
	font-weight: 400;
	line-height: 1.1;
  font-family: "Antenna-Light", sans-serif;
}

// Header Styles
h1 a, h2 a, h3 a, h4 a, h5 a, h6 a { font-weight: inherit; }
h1 { font-size: $h1-fontsize; line-height: 110%; margin: ($h1-fontsize / 2) 0 ($h1-fontsize / 2.5) 0;}
h2 { font-size: $h2-fontsize; line-height: 110%; margin: ($h2-fontsize / 2) 0 ($h2-fontsize / 2.5) 0;}
h3 { font-size: $h3-fontsize; line-height: 110%; margin: ($h3-fontsize / 2) 0 ($h3-fontsize / 2.5) 0; text-transform: uppercase;}
h4 { font-size: $h4-fontsize; line-height: 110%; margin: ($h4-fontsize / 2) 0 ($h4-fontsize / 2.5) 0; text-transform: uppercase;}
h5 { font-size: $h5-fontsize; line-height: 110%; margin: ($h5-fontsize / 2) 0 ($h5-fontsize / 2.5) 0;}
h6 { font-size: $h6-fontsize; line-height: 110%; margin: ($h6-fontsize / 2) 0 ($h6-fontsize / 2.5) 0;}

// Text Styles
em { font-style: italic; }
strong { font-weight: 500; }
small { font-size: 75%; }
.light { font-weight: 300; }
.thin { font-weight: 200; }


.flow-text{
  font-weight: 300;
  $i: 0;
  @while $i <= $intervals {
    @media only screen and (min-width : 360 + ($i * $interval-size)) {
      font-size: 1.2rem * (1 + (.02 * $i));
    }
    $i: $i + 1;
  }

  // Handle below 360px screen
  @media only screen and (max-width: 360px) {
    font-size: 1.2rem;
  }
}